import { useState } from "react";
import "./App.css";
import Search from "./components/search/Search";
import CurrentWeather from "./components/Weather/CurrentWeather";
import { WEATHER_API_URL, WEATHER_API_KEY } from "./Api";
import Forecast from "./components/Forcast/Forecast";
import Pollution from "./components/Pollution/Pollution";
import Header from "./components/Layout/Header";
import Footer from "./components/Layout/Footer";

import "bootstrap/dist/css/bootstrap.min.css";

function App() {
    const [currentWeather, setCurrentWeather] = useState(null);
    const [forecast, setForecast] = useState(null);
    const [pollution, setPollution] = useState(null);

    const handleOnSearchChange = (searchData) => {
        const [lat, lon] = searchData.value.split(" ");

        const currentWeatherFetch = fetch(
            `${WEATHER_API_URL}/weather?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}&units=metric`
        );
        const forecastFetch = fetch(
            `${WEATHER_API_URL}/forecast?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}&units=metric`
        );

        const pollutionFetch = fetch(
            `${WEATHER_API_URL}/air_pollution?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}`
        );

        Promise.all([currentWeatherFetch, forecastFetch, pollutionFetch])
            .then(async (response) => {
                const weatherResponse = await response[0].json();
                const forecastResponse = await response[1].json();
                const pollutionResponse = await response[2].json();
                setCurrentWeather({
                    city: searchData.label,
                    ...weatherResponse,
                });
                setForecast({ city: searchData.label, ...forecastResponse });
                setPollution({ city: searchData.label, ...pollutionResponse });
            })
            .catch((err) => {
                console.log(err);
            });
    };

    //background experiment
    /* console.log("cw",currentWeather );
    const bgDisplay = currentWeather.weather[0];
    console.log(bgDisplay); */

    //

    return (
        <div className="min-vh-100 BGcolor">
            <Header />
            <div className=" mw-auto mx-auto m-0">
                <Search onSearchChange={handleOnSearchChange} />
                <div className="d-flex flex-row justify-content-center align-items-center">
                    {currentWeather && <CurrentWeather data={currentWeather} />}
                    {pollution && <Pollution data={pollution} />}
                </div>
                {forecast && <Forecast data={forecast} />}
            </div>
            <Footer />
        </div>
    );
}

export default App;
