// eslint-disable-next-line no-unused-vars
import React, { useState } from "react";
import "./pollution.css";

const Pollution = ({ data }) => {
    data = data.list[0];

    function AQI(value) {
        switch (value) {
            case 1:
                return (
                    <div className="pollutonTop good">
                        <p className=" pollutionDes ">Good</p>
                    </div>
                );
            case 2:
                return (
                    <div className="pollutonTop fair">
                        <p className=" pollutionDes ">Fair</p>
                    </div>
                );
            case 3:
                return (
                    <div className="pollutonTop moderate">
                        <p className=" pollutionDes ">Moderate</p>
                    </div>
                );
            case 4:
                return (
                    <div className="pollutonTop poor">
                        <p className=" pollutionDes ">Poor</p>
                    </div>
                );
            case 5:
                return (
                    <div className="pollutonTop vpoor">
                        <p className=" pollutionDes ">Very Poor</p>
                    </div>
                );

            default:
                break;
        }
    }

    const poll = [
        "pollution weatherBox good",
        "pollution weatherBox fair",
        "pollution weatherBox moderate",
        "pollution weatherBox poor",
        "pollution weatherBox vpoor",
    ];

    return (
        <div className={poll[data.main.aqi - 1]}>
            <div className="parameter-row ">
                <p className="AQI">AQI</p>
                {AQI(data.main.aqi)}
            </div>

            <div className="pollutionbottom">
                <p className="details">
                    <span className="parameter-row marginB">
                        <span className="parameter-label top ">Pollutants</span>
                        <span className="parameter-label top ">g/m</span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">
                            Carbon Monoxide (CO)
                        </span>
                        <span className="parameter-value">
                            {Math.round(data.components.co)}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Ammonia (NH)</span>
                        <span className="parameter-value">
                            {data.components.nh3}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">
                            Nitrogen Monoxide (NO)
                        </span>
                        <span className="parameter-value">
                            {Math.round(data.components.no)}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">
                            Nitrogen Dioxide (NO)
                        </span>
                        <span className="parameter-value">
                            {data.components.no2}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Ozone (O)</span>
                        <span className="parameter-value">
                            {data.components.o3}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">PM.</span>
                        <span className="parameter-value">
                            {data.components.pm2_5}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">PM</span>
                        <span className="parameter-value">
                            {data.components.pm10}
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">
                            Sulfur dioxide (SO)
                        </span>
                        <span className="parameter-value">
                            {data.components.so2}
                        </span>
                    </span>
                </p>
            </div>
        </div>
    );
};

export default Pollution;
