import React from "react";
import {
    Accordion,
    AccordionItem,
    AccordionItemButton,
    AccordionItemHeading,
    AccordionItemPanel,
} from "react-accessible-accordion";
import "./forecast.css";

const WEEK_DAY = [
    "Sunday",
    "Monday",
    "Tuestday",
    " Wednesday",
    "Thrusday",
    "Friday",
    "Saturday",
];

const Forecast = ({ data }) => {
    //week days
    const dayInWeek = new Date().getDay();
    const forecastDays = WEEK_DAY.slice(dayInWeek, WEEK_DAY.length).concat(
        WEEK_DAY.slice(0, dayInWeek)
    );

    //week forecast
    const weatherDay = [];
    for (let index = 0; index < data.list.length; index++) {
        if (data.list[index].dt_txt.split(" ")[1] === "09:00:00") {
            const element = data.list[index];
            weatherDay.push(element);
        }
    }

    //hourly forecast
    function weatherss() {
        let weathers = [];
        for (let idx = 0; idx < weatherDay.length; idx++) {
            weathers[idx] = new Array(4);
        }
        let j;
        let count = 0;
        for (let i = 0; i < weatherDay.length; i++) {
            for (j = 0; j < data.list.length; j += 2) {
                if (
                    data.list[j].dt_txt.split(" ")[0] ===
                        weatherDay[i].dt_txt.split(" ")[0] &&
                    count < 4
                ) {
                    weathers[i][count] = data.list[j];
                    count++;
                }
            }
            count = 0;
        }
        return weathers;
    }
    const hourly = weatherss();

    //wind Direction
    function windDiection(WD) {
        if ((WD < 11.25 && WD > 0) || (WD < 360 && WD > 348.75)) {
            return "North";
        } else if (WD > 11.25 && WD < 33.75) {
            return "North-Northeast";
        } else if (WD > 33.75 && WD < 56.25) {
            return "Northeast";
        } else if (WD > 56.25 && WD < 78.75) {
            return "East-Northeast";
        } else if (WD > 78.75 && WD < 101.25) {
            return "East";
        } else if (WD > 101.25 && WD < 123.75) {
            return "East-Southeast";
        } else if (WD > 123.75 && WD < 146.25) {
            return "Southeast";
        } else if (WD > 146.25 && WD < 168.75) {
            return "South-Southeast";
        } else if (WD > 168.75 && WD < 191.25) {
            return "South";
        } else if (WD > 191.25 && WD < 213.75) {
            return "South-Southwest";
        } else if (WD > 213.25 && WD < 236.25) {
            return "Southwest";
        } else if (WD > 236.25 && WD < 258.75) {
            return "West-Southwest";
        } else if (WD > 258.75 && WD < 281.25) {
            return "West";
        } else if (WD > 281.25 && WD < 303.75) {
            return "West-Northwest";
        } else if (WD > 303.75 && WD < 326.25) {
            return "Northwest";
        } else if (WD > 326.25 && WD < 348.75) {
            return "North-Northwest";
        } else return "error";
    }

    return (
        <div className="mt-4 ms-5  pb-2">
            <div className=" FC-Section Disp-item">
                <label className="FC-text text">Forecast</label>
            </div>
            <Accordion allowZeroExpanded>
                {weatherDay.map((item, idx) => (
                    <AccordionItem key={idx}>
                        <AccordionItemHeading>
                            <AccordionItemButton className="week-Align">
                                <div className="Disp-item week-Section">
                                    <label className="week-text">
                                        {weatherDay[idx].dt_txt.split(" ")[0]}
                                    </label>
                                    <label className="week-text text">
                                        {forecastDays[idx]}
                                    </label>
                                </div>
                            </AccordionItemButton>
                        </AccordionItemHeading>
                        <AccordionItemPanel>
                            <Accordion allowZeroExpanded>
                                {hourly[idx].map((hour, i) => (
                                    <AccordionItem key={i}>
                                        <AccordionItemHeading>
                                            <AccordionItemButton className="hour-Align">
                                                <div className="Disp-item hour-Section">
                                                    <img
                                                        src={`icons/${hour.weather[0].icon}.png`}
                                                        alt="weather"
                                                        className="icon-small"
                                                    />

                                                    <label className="hour-text text description">
                                                        {
                                                            hour.dt_txt.split(
                                                                " "
                                                            )[1]
                                                        }
                                                    </label>
                                                    <label className="description">
                                                        {
                                                            hour.weather[0]
                                                                .description
                                                        }
                                                    </label>
                                                    <label className="temp">
                                                        {Math.round(
                                                            hour.main.temp
                                                        )}
                                                        
                                                    </label>
                                                </div>
                                            </AccordionItemButton>
                                        </AccordionItemHeading>
                                        <AccordionItemPanel>
                                            <div className="daily-details-grid">
                                                <div className="daily-details-grid-item">
                                                    <label>Feels Like</label>
                                                    <label>
                                                        {Math.round(
                                                            hour.main.feels_like
                                                        )}
                                                        
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Pressure</label>
                                                    <label>
                                                        {hour.main.pressure} hPa
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Humidity</label>
                                                    <label>
                                                        {hour.main.humidity}%
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Cloud</label>
                                                    <label>
                                                        {hour.clouds.all}%
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Wind Speed</label>
                                                    <label>
                                                        {(
                                                            hour.wind.speed *
                                                            3.6
                                                        ).toFixed(1)}{" "}
                                                        Km/hr
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Sea Level</label>
                                                    <label>
                                                        {hour.main.sea_level} m
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>
                                                        Wind Direction
                                                    </label>
                                                    <label>
                                                        {windDiection(
                                                            hour.wind.deg
                                                        )}
                                                    </label>
                                                </div>
                                                <div className="daily-details-grid-item">
                                                    <label>Visibility</label>
                                                    <label>
                                                        {item.visibility / 1000}{" "}
                                                        Km
                                                    </label>
                                                </div>
                                            </div>
                                        </AccordionItemPanel>
                                    </AccordionItem>
                                ))}
                            </Accordion>
                        </AccordionItemPanel>
                    </AccordionItem>
                ))}
            </Accordion>
        </div>
    );
};

export default Forecast;
