import React from "react";
import "./currentWeather.css";
import {
    Accordion,
    AccordionItem,
    AccordionItemButton,
    AccordionItemHeading,
    AccordionItemPanel,
} from "react-accessible-accordion";

//error 1  in log due to div and p in this components

const CurrentWeather = ({ data }) => {
    console.log(data);
    //
    //timestamp
    function timeStamp(currTime, timezone, value) {
        timezone = 19800 - timezone;
        var unixTimestamp = currTime - timezone;
        var date = new Date(unixTimestamp * 1000);
        switch (value) {
            case 1:
                return date.toLocaleDateString("it-IT");
            case 2:
                return date.toLocaleTimeString("it-IT");
            default:
                break;
        }
    }

    // function timeZone(timezone) {
    //     timezone = 19800 - timezone;
    //     var unixTimestamp = timezone;
    //     var date = new Date(unixTimestamp * 1000);
    //     return date.toLocaleTimeString("it-IT");
    // }

    return (
        <div className="weatherBox rounded-3 mx-auto mt-4 mb-4 px-2 pt-0 pb-2 text-white bgcolor">
            <div className="d-flex justify-content-around align-items-center">
                <div>
                    <p className="city m-0 mt-2 ms-1">{data.city}</p>
                    <p className="weather-des m-0 ms-1 mt-2">
                        {data.weather[0].description}
                    </p>
                </div>
                <img
                    className="weather-icon"
                    src={`icons/${data.weather[0].icon}.png`}
                    alt="weather"
                />
            </div>
            <div className="d-flex justify-content-evenly align-items-center">
                <p className="temperature w-auto">
                    {Math.round(data.main.temp)}
                </p>
                <p className="details">
                    <span className="parameter-row justify-content-evenly">
                        <span className="parameter-label top mb-2">
                            Details
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Feels like</span>
                        <span className="parameter-value">
                            {Math.round(data.main.feels_like)} 
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Wind</span>
                        <span className="parameter-value">
                            {(data.wind.speed * 3.6).toFixed(1)} Km/hr
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Humidity</span>
                        <span className="parameter-value">
                            {Math.round(data.main.humidity)}%
                        </span>
                    </span>
                    <span className="parameter-row">
                        <span className="parameter-label">Pressure</span>
                        <span className="parameter-value">
                            {data.main.pressure} hPa
                        </span>
                    </span>
                </p>
            </div>

            <Accordion allowZeroExpanded>
                <AccordionItem>
                    <AccordionItemHeading>
                        <AccordionItemButton>
                            <label className="ms-2 text-decoration-underline fw-lighter text-secondary mb-2">
                                More Info
                            </label>
                        </AccordionItemButton>
                    </AccordionItemHeading>
                    <AccordionItemPanel>
                        <div>
                            <div className="details py-0">
                                <span className="parameter-row  h-25">
                                    <span className="parameter-label text-white">
                                        Date
                                    </span>
                                    <span className="parameter-value text-white">
                                        {timeStamp(data.dt, data.timezone, 1)}
                                    </span>
                                </span>
                                <span className="parameter-row  h-25">
                                    <span className="parameter-label text-white">
                                        Sunrise Time
                                    </span>
                                    <span className="parameter-value text-white">
                                        {timeStamp(
                                            data.sys.sunrise,
                                            data.timezone,
                                            2
                                        )}
                                    </span>
                                </span>
                                <span className="parameter-row">
                                    <span className="parameter-label">
                                        Sunset Time
                                    </span>
                                    <span className="parameter-value">
                                        {timeStamp(
                                            data.sys.sunset,
                                            data.timezone,
                                            2
                                        )}
                                    </span>
                                </span>
                                <span className="parameter-row">
                                    <span className="parameter-label">
                                        Cloud
                                    </span>
                                    <span className="parameter-value">
                                        {data.clouds.all} %
                                    </span>
                                </span>
                                <span className="parameter-row">
                                    <span className="parameter-label">
                                        Max Temperature
                                    </span>
                                    <span className="parameter-value">
                                        {Math.round(data.main.temp_max)} 
                                    </span>
                                </span>
                                <span className="parameter-row">
                                    <span className="parameter-label">
                                        Min Temperature
                                    </span>
                                    <span className="parameter-value">
                                        {Math.round(data.main.temp_min)} 
                                    </span>
                                </span>
                                <span className="parameter-row">
                                    <span className="parameter-label">
                                        Visibility
                                    </span>
                                    <span className="parameter-value">
                                        {data.visibility / 1000} Km
                                    </span>
                                </span>
                            </div>
                        </div>
                    </AccordionItemPanel>
                </AccordionItem>
            </Accordion>
        </div>
    );
};

export default CurrentWeather;
