import React from "react";
import { Container } from "reactstrap";

const Footer = () => {
    return (
        <div className="footer-sticky">
            <Container
                fluid
                tag="footer"
                className="fixed-bottom text-center HeadFootBG text-white text-uppercase pt-2 pb-2"
            >
                <div className="mb-1 pb-1">
                    <a href="https://www.linkedin.com/in/abhinav0115/">
                        <img
                            className="FooterLogo bg-white rounded me-3"
                            src="https://cdn-icons-png.flaticon.com/512/145/145807.png"
                            alt="Linkedin"
                        />
                    </a>
                    <a href="https://github.com/Abhinav0115/">
                        <img
                            className="FooterLogo bg-white rounded"
                            src="https://cdn-icons-png.flaticon.com/512/25/25231.png"
                            alt="Github"
                        />
                    </a>
                </div>
                <span className="text-warning fw-bold">&#169; GetWeather,</span>{" "}
                weather forecasting App build using{" "}
                <span className="text-warning ">Openweathermap.org </span>And
                <span className="text-warning "> Geodb Cities API</span>
            </Container>
        </div>
    );
};

export default Footer;
